.btn {
    margin: 1rem;
    transition: all .3s ease-in-out;
}

.btn:hover {
    cursor: pointer;
    -webkit-transform: translateY(-1px);
    transform: translateY(-1px);
    box-shadow: 0 3px 6px rgba(38, 38, 38, 0.2), 0 3px 6px rgba(38, 38, 38, 0.2);
}

.micControls {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

#mic {
    padding: 2px;
    margin: 16px;
    margin-top: 18px;
    opacity: 0.3;
    transition: all .3s ease-in-out;
}

#mic.active{
    -webkit-animation: pulse 1.5s ease-in-out infinite;
            animation: pulse 1.5s ease-in-out infinite;
}

@-webkit-keyframes pulse {
    0% {
      opacity: 0.3;
    }
    50% {
      opacity: 1.0;
    }
    100% {
        opacity: 0.3;
    }
  }

@keyframes pulse {
    0% {
      opacity: 0.3;
    }
    50% {
      opacity: 1.0;
    }
    100% {
        opacity: 0.3;
    }
  }
  
#notes {
    resize: none;
    overflow: auto;
    margin: 2rem;
    border: 1px solid black;
    padding: 1rem;
    font-size: 1.5rem;
    width: 80%;;
    height: auto;
    border-radius: 10px;
}

i {
    margin-right: .5rem;
}

